.Button {
    display: flex;
    align-items: center;
    margin: 2px;
}

.Button button {
    width: 100px;
    height: 30px;
    font-size: 20px;
    background-color: #ffffff;
    cursor: pointer;
}

.Button button:hover {
    background-color: #e6e6e6;
    transition: all 0.25s linear;
}